.header {
  // width: 100vw;
  height: 10vh;
  display: flex;
  padding: 0 5%;
  justify-content: space-between;
  min-width: 1000px;
  // align-items: center;
  background-color: #a31432;
}

.title {
  display: flex;
  align-items: center;
  cursor: pointer;
  &-img {
    margin-right: 0.4rem;
    width: 4rem;
    height: 4rem;
  }
}
.logo {
  font-size: 16px;
  font-weight: 700;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &-first {
    letter-spacing: 0.5em;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  &-userInfo {
    color: #ffffff;
    display: flex;
    justify-content: end;
    align-items: center;
    font-size: 13px;
    &-name {
      text-decoration: underline;
      cursor: pointer;
    }
    &-name:hover {
      font-weight: 800;
    }

    &-logout {
      cursor: pointer;
      margin-left: 2rem;
      border: 1.5px solid #ffffff;
      border-radius: 15px;
      background-color: transparent;
      color: #ffffff;
    }
  }
  &-buttons {
    margin: 0;
    display: flex;

    &-button {
      font-size: 12px;
      margin-left: 2rem;
      padding: 0.2rem 1.5rem;
      text-decoration: none;
      background-color: #ffffff;
      border: 0;
      border-radius: 15px;
      font-family: "NotoSansKR";
      &-a {
        text-decoration: none;
        color: #a31432;
        font-weight: 900;
        font-size: larger;
      }
    }
    &-button_false {
      font-family: "NotoSansKR";
      font-size: 12px;
      margin-left: 2rem;
      padding: 0.2rem 1.5rem;
      text-decoration: none;
      background-color: #a31432;
      border: 0;
      border-radius: 15px;
      &-a {
        text-decoration: none;
        color: #ffffff;
        font-weight: 900;
        font-size: larger;
      }
    }
    &-dropdown {
      font-size: 14.4px;
      margin-left: 2rem;
      padding: 0.2rem 1.5rem;
      text-decoration: none;
      background-color: #ffffff;
      border: 0;
      border-radius: 15px;
      font-family: "NotoSansKR";
      text-decoration: none;
      color: #a31432;
      font-weight: 900;
      &_false {
        font-family: "NotoSansKR";
        font-size: 14.4px;
        margin-left: 2rem;
        padding: 0.2rem 1.5rem;
        text-decoration: none;
        background-color: #a31432;
        border: 0;
        border-radius: 15px;
        text-decoration: none;
        color: #ffffff;
        font-weight: 900;
      }
      &-ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        text-align: center;
        list-style: none;
        background-color: #a31432;
        width: 120px;
        padding: 10px 20px;
        border-radius: 10px;
        height: 65px;
        &-button {
          background-color: #a31432;
          border: none;
          margin-bottom: 10px;
          &-link {
            font-size: 14.4px;
            color: white;
            text-decoration: none;    
          }
          :hover {
            font-weight: 900;
          }
        }
        
      }
    }
  }
}
