.recommended-lecture {
  width: 100%;
  height: 100%;
  min-height: 1080px;
  &-header {
    position: sticky;
    width: 100%;
    height: 5rem;
    top: 0;
    background: #a31432;
    z-index: 10;
  }
  &-filterbutton {
    position: relative;
    cursor: pointer;
    display: block;
    margin: auto;
    top: 3rem;
    width: 15rem;
    height: 5rem;
    background: rgb(255, 255, 255);
    border-radius: 30px;
    border-style: inherit;
    border: 1px solid black;
    margin-top: 15px;
    margin-bottom: 5px;
    transition: all 0.2s;
    &-icon {
      position: relative;
    }
    &-word {
      margin-left: 10px;
      font-family: "Inter";
      font-style: normal;
      font-weight: 600;
      font-size: 30px;
      line-height: 36px;
    }
  }
  &-filterbutton:hover {
    width: 15.5rem;
    height: 5.5rem;
    transition: all 0.2s;
  }

  &-upbutton{
    position: fixed;
    bottom: 60px;
    right: 70px;
    button{
      border: none;
      background-color: #920f0fa8;
      color: white;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      cursor: pointer;
    }
    button:hover{
      background-color: #920f0f83;
    }
  }

  &-loading {
    position: relative;
    top: 50px;
  }
  &-list {
    ul {
      position: relative;
      top: -30px;
      list-style: none;
      padding-top: -50px;
      padding-left: 0px;
      padding-bottom: 80px;
      margin-bottom: 80px;
      margin-top: 50px;
    }
    &-exp {
      background-color: rgb(143, 31, 31);
      position: relative;
      top: 80px;
      display: flex;
      margin: auto;
      margin-bottom: 80px;
      max-width: 1510px;
      min-width: 870px;
      padding-left: 1%;
      padding-right: 2%;
      padding-top: 1%;
      padding-bottom: 1%;
      width: 70%;
      height: 40px;
      line-height: 40px;
      text-align: center;
      justify-content: space-between;
      border-radius: 30px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      color: white;
      &-title {
        font-weight: 800;
        font-size: 30px;
        display: inline-block;
        width: 380px;
        min-width: 150px;
      }
      &-exp {
        font-weight: 100;
        font-size: 20px;
        padding-left: 3%;
        width: 120px;
      }
      &-exp1{
        position: relative;
        font-size: 20px;
        font-weight: 100;
        width: 80px;
      }
      &-rank {
        // color: #a31432;
        color: white;
        font-weight: 100;
        font-size: 18px;
        padding-left: 3%;
        width: 100px;
      }
    }
    &-notice{
      position: relative;
      font-size: 0.8rem;
      font-weight: 100;
      width: fit-content;
      margin: auto;
      top: 70px;
    }
  }
}
