.graduation {
  min-width: 1000px;
  &-total {
    position: relative;
    background-color: white;
    top: 100px;
    width: 65%;
    min-width: 700px;
    height: 50px;
    border: solid #000000 1px;
    margin: auto;
    margin-bottom: 100px;
    border-radius: 15px;
    box-shadow: 2px 4px 15px #a8a8a8;
    padding: 10px 10px 10px 10px;
    &-title {
      position: relative;
      left: 0px;
      font-size: 2rem;
      font-weight: 800;
      top: -70px;
      border-bottom: 3px solid #c74444;
    }
    &-score {
      position: relative;
      font-size: 2rem;
      font-weight: 800;
      text-align: center;
      justify-content: space-between;
      top: -40px;
      &1 {
        color: #c74444;
      }
    }
  }
  &-major {
    background-color: white;
    position: relative;
    top: 100px;
    width: 65%;
    min-width: 700px;
    height: 400px;
    border: solid #000000 1px;
    margin: auto;
    border-radius: 15px;
    box-shadow: 2px 4px 15px #a8a8a8;
    padding: 10px 10px 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    &-title {
      position: absolute;
      left: 10px;
      font-size: 2rem;
      height: 2.7rem;
      font-weight: 800;
      top: -60px;
      border-bottom: 3px solid #c74444;
      &-txt {
        position: absolute;
        left: 80px;
        top: -50px;
        font-size: 0.8rem;
        font-weight: 100;
        line-height: 2.7rem;
        border: none;
      }
    }
    &-1 {
      flex-basis: 50%;
      height: 85%;
      margin: 40px 10px 10px 10px;
      padding: 0;
      border-radius: 15px;
      &-txt {
        position: absolute;
        left: 25px;
      }
      &-title {
        position: relative;
        font-size: 1.5rem;
        // border-bottom: 1px solid #c74444;
      }
      &-score {
        position: relative;
        margin-left: 10px;
        font-size: 1.25rem;
        font-weight: 800;
        border-radius: 30px;
        padding: 2px 20px;
        color: white;
        background-color: #c74444;
        &-complete {
          position: relative;
          margin-left: 10px;
          font-size: 1.25rem;
          font-weight: 800;
          border-radius: 30px;
          padding: 2px 20px;
          color: white;
          background-color: #129423;
        }
      }
    }
    &-2 {
      flex-basis: 50%;
      height: 85%;
      margin: 40px 10px 10px 10px;
      padding: 0;
      border-radius: 15px;
      &-txt {
        position: absolute;
        left: 50%;
      }
      &-title {
        position: relative;
        font-size: 1.5rem;
        // border-bottom: 1px solid #c74444;
      }
      &-score {
        position: relative;
        font-size: 1.25rem;
        margin-left: 10px;
        border-radius: 30px;
        font-weight: 800;
        padding: 2px 20px;
        color: white;
        background-color: #c74444;
        &-complete {
          position: relative;
          font-size: 1.25rem;
          margin-left: 10px;
          border-radius: 30px;
          font-weight: 800;
          padding: 2px 20px;
          color: white;
          background-color: #129423;
        }
      }
    }
    ul {
      border: 1px solid black;
      list-style: none;
      overflow-y: scroll;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
    }
    ul::-webkit-scrollbar {
      display: none; /* Chrome, Safari, Opera*/
    }
  }
  &-GE {
    position: relative;
    top: 200px;
    width: 65%;
    min-width: 700px;
    height: 700px;
    border: solid #000000 1px;
    margin: auto;
    margin-bottom: 100px;
    border-radius: 15px;
    box-shadow: 2px 4px 15px #a8a8a8;
    padding: 10px 10px 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    &-area {
      margin: auto;
      position: relative;
      top: -150px;
      z-index: 100;
      height: 250px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      width: 65%;
      min-width: 700px;
      // border: 1px solid black;
      border-radius: 15px;
      &-title {
        position: absolute;
        top: -60px;
        font-size: 1.3rem;
      }
      &-txt {
        position: absolute;
        font-size: 0.9rem;
        top: -30px;
        &-red {
          color: red;
        }
      }
      &-area {
        font-size: 1.3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px 10px 15px 10px;
        flex-basis: 30%;
        margin-bottom: 10px;
        border-radius: 15px;
        border: 1px solid rgb(0, 0, 0);
        // background-color: #c5c5c52d;
        &-title {
          display: inline-block;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        &-taken {
          font-size: 1.2rem;
          font-weight: 100;
          position: relative;
          top: 15px;
          background-color: #129423;
          border-radius: 30px;
          padding: 5px 5px;
          text-align: center;
          color: white;
          width: 70px;
        }
        &-untaken {
          font-size: 1.2rem;
          font-weight: 100;
          position: relative;
          top: 15px;

          background-color: #c74444;
          width: 70px;
          border-radius: 30px;
          padding: 5px 5px;
          color: white;
          text-align: center;
        }
      }
    }
    &-title {
      position: absolute;
      left: 10px;
      font-size: 2rem;
      height: 2.7rem;
      font-weight: 800;
      top: -60px;
      border-bottom: 3px solid #c74444;
    }
    &-1 {
      flex-basis: 50%;
      height: 340px;
      margin: 40px 10px 10px 10px;
      padding: 0;
      border-radius: 15px;
      &-txt {
        position: absolute;
        left: 25px;
      }
      &-title {
        position: relative;
        font-size: 1.3rem;
        // border-bottom: 1px solid #c74444;
      }
      &-score {
        position: relative;
        margin-left: 10px;
        font-size: 1.25rem;
        font-weight: 800;
        border-radius: 30px;
        padding: 2px 10px;
        color: white;
        background-color: #c74444;
        &-complete {
          position: relative;
          font-size: 1.25rem;
          margin-left: 10px;
          border-radius: 30px;
          font-weight: 800;
          padding: 2px 10px;
          color: white;
          background-color: #129423;
        }
      }
    }
    &-2 {
      flex-basis: 50%;
      height: 340px;
      margin: 40px 10px 10px 10px;
      padding: 0;
      border-radius: 15px;
      &-txt {
        position: absolute;
        left: 35%;
      }
      &-title {
        position: relative;
        font-size: 1.3rem;
        // border-bottom: 1px solid #c74444;
      }
      &-score {
        position: relative;
        font-size: 1.25rem;
        margin-left: 10px;
        border-radius: 30px;
        font-weight: 800;
        padding: 2px 10px;
        color: white;
        background-color: #c74444;
        &-complete {
          position: relative;
          font-size: 1.25rem;
          margin-left: 10px;
          border-radius: 30px;
          font-weight: 800;
          padding: 2px 10px;
          color: white;
          background-color: #129423;
        }
      }
    }
    &-3 {
      flex-basis: 50%;
      height: 340px;
      margin: 40px 10px 10px 10px;
      padding: 0;
      border-radius: 15px;
      &-txt {
        position: absolute;
        left: 68%;
      }
      &-title {
        position: relative;
        font-size: 1.25rem;
        // border-bottom: 1px solid #c74444;
      }
      &-score {
        position: relative;
        font-size: 1.25rem;
        margin-left: 10px;
        border-radius: 30px;
        font-weight: 800;
        padding: 2px 10px;
        color: white;
        background-color: #c74444;
        &-complete {
          position: relative;
          font-size: 1.25rem;
          margin-left: 10px;
          border-radius: 30px;
          font-weight: 800;
          padding: 2px 10px;
          color: white;
          background-color: #129423;
        }
      }
    }
    ul {
      border: 1px solid black;
      list-style: none;
      overflow-y: scroll;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
    }
    ul::-webkit-scrollbar {
      display: none; /* Chrome, Safari, Opera*/
    }
  }
  &-margin {
    position: relative;
    display: block;
    top: -70px;
    width: 100%;
    height: 150px;
    p {
      position: relative;
      width: fit-content;
      margin: auto;
      font-size: 0.7rem;
      color: #00000048;
      top: 70px;
    }
  }
  &-excel-reupload {
    position: relative;
    top: -70px;
    display: flex;
    width: 30%;
    min-width: 500px;
    margin: auto;
    margin-bottom: 50px;
    justify-content: space-around;
    border: 1px solid black;
    padding: 10px 10px;
    border-radius: 30px;
    box-shadow: 2px 4px 15px #a8a8a8;
    span {
      font-size: 1.3rem;
      font-weight: 100;
    }
  }
}

.graduation-excel {
  &-title {
    position: relative;
    width: fit-content;
    font-size: 2.25rem;
    font-weight: 800;
    top: 80px;
    margin: auto;
    border-bottom: 3px solid #c74444;
  }
  &-upload {
    position: relative;
    width: 50%;
    min-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    border-radius: 15px;
    margin: auto;
    top: 130px;
    height: 200px;
    box-shadow: 2px 4px 15px #a8a8a8;
    &-txt1 {
      position: relative;
      top: 50px;
      flex-basis: 20%;
      font-size: 1.75rem;
      font-weight: 100;
    }
    input {
      position: relative;
      top: 80px;
      cursor: pointer;
    }
    &-txt2 {
      position: relative;
    }
    &-file {
      position: relative;
      top: 80px;
    }
  }
  &-upload-help {
    position: relative;
    margin: auto;
    width: 50%;
    min-width: 850px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    top: 200px;
    padding: 30px 10px;
    border-radius: 30px;
    &-title {
      font-size: 1.5rem;
      margin-bottom: 30px;
    }
    &-txt1 {
      margin-bottom: 30px;
    }
    &-link {
      text-decoration: none;
      color: #c74444;
    }
  }
}
